body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.homePage {
  height:99.9vh;
  background-image: url(/static/media/wallpaper1.f6700053.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

.gameName {
  position: relative;
  top: 5%;
  left: 5%;
  -webkit-transform: translate(-50%,-50%) !important;
          transform: translate(-50%,-50%) !important;
  -webkit-animation: santa 5s linear infinite;
          animation: santa 5s linear infinite;
}

.santa {
  height: 170px;
}

@-webkit-keyframes santa {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0); }
  50% { -webkit-transform: translate(20px,20px); transform: translate(20px,20px); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

@keyframes santa {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0); }
  50% { -webkit-transform: translate(20px,20px); transform: translate(20px,20px); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

.greetingText {
  position: absolute;
  top: 20%;
  left: 55%;
  width: 70%;
  -webkit-transform: translate(-50%,-50%) !important;
          transform: translate(-50%,-50%) !important;
}

.greetingText span {
  color: #fff;
  position: relative;
  font-family: 'Charm', cursive;
  font-size: 5em;
  text-align: center;
  font-weight: bold;
  text-shadow: 0px 10px 20px rgba(0,0,0,0.5);
}

.colorRed {
  color:#c90a0a !important;
}

.infoText .line1{
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  color: #fff;
  font-family: 'Charm', cursive;
  font-size: 4em;
  font-weight: bold;
  text-shadow: 0px 10px 20px rgba(0,0,0,0.5);
}

.infoText .line2{
  position: absolute;
  top: 29%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  color: #c90a0a;
  font-family: 'Charm', cursive;
  font-size: 4em;
  font-weight: bold;
  text-shadow: 0px 10px 20px rgba(0,0,0,0.5);
}

.arrow {
  position: fixed;
  left: 50%;
  bottom: 15px;
  margin-left: -15px;
}

.outerContainer {
  height: 99.5vh;
  background: #125e84;
  align-content: center;
  align-self: center;
}

.heroesContainer {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  align-content: center;
}


@-webkit-keyframes heroes {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0); }
  50% { -webkit-transform: translate(20px,20px); transform: translate(20px,20px); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}


@keyframes heroes {
  0% { -webkit-transform: translate(0,0); transform: translate(0,0); }
  50% { -webkit-transform: translate(20px,20px); transform: translate(20px,20px); }
  100% { -webkit-transform: translate(0,0); transform: translate(0,0); }
}

.characters {
  height:180px;
  display: block;
  margin: 20px auto;
  -webkit-animation: heroes 5s linear infinite;
          animation: heroes 5s linear infinite;
  align-content: center;
}

.heroesContainer h6{
  text-align: center;
  font-family: 'Charm', cursive;
  color: #fff;
  font-size: 30px;
  margin: 10px 0px;
  padding: 0px;
}

.outerContainer h1 {
  margin: 0px;
  padding: 0px;
  text-align: center;
  color: #fff;
  position: relative;
  font-family: 'Charm', cursive;
  font-size: 3em;
}

.outerContainer h1:hover {
  color: #c90a0a;
}
.runBulb {
  cursor: pointer;
}

.bulb {
  width: 40px;
  height: 55px;
}

.customStyle{
  font-family: 'Comfortaa', cursive !important;
  background: #125e84;
}
.options{
 cursor: pointer;
}
